@font-face 
  font-family: RussoOne
  src: url("Russo_One.ttf")

html,body 
    font-family: RussoOne
    height: 100vh 
    margin: 0 

canvas#game
    background: black 
    height: 100vh 
    width: 100vw
    display: block

score-viewer
    display: block

#gameover-screen
    color: white
    width: 100vw
    height: 100vh 
    z-index: 12
    position: absolute
    display: flex 
    flex-direction: column
    justify-content: center
    align-items: center 
    align-content: center
    background-color: #292727bd
    > div.gameover-score
        > h1 
            font-weight: bold
        display: flex 
        flex-direction: column 
        justify-content: center 
        align-items: center
    > ul 
        justify-content: center
        list-style: decimal-leading-zero
    > div.player-placement
        display: flex 
        flex-direction: column 
        justify-content: center
        align-items: center 
        align-content: center
        margin-bottom: 20px

.game-controls 
    color: white
    position: absolute
.game-options
    z-index: 124
    color: white
.sidenav-header
    display: flex 
    flex-direction: row 
    justify-content: left 
    > h5 
        width: auto !important
.w3-sidebar 
    z-index: 125

.lds-facebook 
    display: inline-block
    position: relative
    width: 80px
    height: 80px
    & div 
        display: inline-block
        position: absolute
        left: 8px
        width: 16px
        background: #fff
        animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite
        &:nth-child(1) 
            left: 8px
            animation-delay: -0.24s
        &:nth-child(2)
            left: 32px
            animation-delay: -0.12s
        &:nth-child(3)
            left: 56px
            animation-delay: 0
@keyframes lds-facebook 
  0% 
    top: 8px
    height: 64px
  50%, 100% 
    top: 24px
    height: 32px

.scoreboard 
    z-index: 10
    color: white
    display: flex 
    flex-direction: column
    justify-content: center 
    align-content: center 
    align-items: center
    position: absolute
    height: 100vh 
    width: 100vw    
    background-color: #292727bd
    > ul 
        overflow-y: scroll
        scorllbar-width: thin
        justify-content: center
        list-style: decimal-leading-zero
        > li 
            margin-right: 10px
.score
    color: gray
    text-decoration: wavy

                 